<!-- 创建维修单  -->
<template>
  <view class="save-tasks">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :background="background"
        title="创建维修单"></u-navbar>
    <view class="form-wrapper bg-white margin-normal radius-normal padding-normal">
      <u-form :model="form" ref="uForm" label-width="160">
        <u-form-item label="故障原因" prop="content" :required="true" :borderBottom="false">
          <u-input type="textarea"
                   placeholder="简要描述故障原因（至少 5 个字）"
                   height="300"
                   placeholder-style="color: #D4D4D5; line-height: 1; font-size: 14px;"
                   v-model.trim="form.content"/>
        </u-form-item>
      </u-form>
    </view>
    <view class="save-tasks-inner-wrapper margin-normal radius-normal">
      <u-button v-if="showBtn1" ripple type="success" :loading="loading" @click="saveTasks">完成创建</u-button>
    </view>

    <u-toast ref="uToast"/>

    <u-modal v-model="showModal" @confirm="confirm" @cancel="loading = false" show-cancel-button content="确认提交？"></u-modal>
  </view>
</template>

<script>

  export default {
    name: "save-tasks",
    data() {
      return {
        showBtn1: false,
        background: {
          'background': '#f2f2f8'
        },
        showModal: false,
        loading: false,
        form: {
          content: "",
          id: ""
        },
        rules: {
          content: [
            {
              required: true,
              min: 5,
              message: '简要描述故障原因',
              trigger: 'change'
            }
          ]
        }
      }
    },
    computed: {
      PREMISS() {
        return this.$store.state.PREMISS
      }
    },
    onLoad() {
      //  建单的权限
      const premissObj = uni.getStorageSync("premissObj");
      this.showBtn1 = !!(premissObj && premissObj[this.PREMISS.createForm])
    },
    onReady() {
      this.$refs.uForm.setRules(this.rules);
    },
    methods: {
      confirm() {
        this.loading = true;
        this.$u
            .post("/maint/saveTask", this.form)
            .then(res => {
              this.$refs.uToast.show({
                title: res.message,
                type: res.code === 200 ? "success" : "warning"
              });
              if(res.code === 200) {
                this.$store.commit("freshTaskFunc", true);
                setTimeout(() => {
                  uni.navigateTo({
                    url: '/pages/home/index',
                  })
                }, 300)
              }
            })
            .catch(err => {
            }).finally(_ => {
              this.loading = false;
        })
      },
      saveTasks() {
        if (this.loading) {
          return
        }
        this.$refs.uForm.validate(valid => {
          this.showModal = valid;
        });
      },
    }
  }
</script>

<style scoped lang="scss">
  uni-page-body {
    height: 100%;
  }

  .save-tasks {
    height: 100%;
  }
</style>